// @ts-nocheck
import React, { Component } from 'react';
// import items from './data'
import Client from './Contentful';

// @ts-ignore
const RoomContext = React.createContext();

class RoomProvider extends Component {
	state = {
		rooms: [],
		sortedRooms: [],
		featuredRooms: [],
		loading: true,
		type: 'all',
		capacity: 1,
		price: 0,
		minPrice: 0,
		maxPrize: 0,
		minSize: 0,
		maxSize: 0,
		breakfast: false,
		pets: false,
	};

	getData = async () => {
		try {
			let response = await Client.getEntries({
				content_type: 'resortData',
				order: 'sys.createdAt',
			});
			console.log(response.items);
			let rooms = this.formatData(response.items);
			let featuredRooms = rooms.filter(room => room.featured === true);
			let maxPrize = Math.max(...rooms.map(item => item.price));
			let maxSize = Math.max(...rooms.map(item => item.size));
			this.setState({
				rooms,
				featuredRooms,
				sortedRooms: rooms,
				loading: false,
				price: maxPrize,
				maxPrize,
				maxSize,
			});
		} catch (error) {}
	};

	componentDidMount() {
		this.getData();
		// let rooms = this.formatData(items)
		// let featuredRooms = rooms.filter(room => room.featured === true)
		// let maxPrize = Math.max(...rooms.map(item => item.price))
		// let maxSize = Math.max(...rooms.map(item => item.size))
		// this.setState({
		//     rooms,
		//     featuredRooms,
		//     sortedRooms: rooms,
		//     loading: false,
		//     price: maxPrize,
		//     maxPrize,
		//     maxSize
		// })
	}

	/**
	 * @description
	 * @author Sujay Alaspure
	 * @param {{sys:{id:number}, fields: {name: string, slug: string, type: string, price: number, size: number, capacity: number, pets: boolean, breakfast: boolean, featured: boolean, description:string}} items
	 * @returns {*}
	 * @memberof RoomProvider
	 */
	formatData(items) {
		let tempItems = items.map(item => {
			let id = item.sys.id;
			let images = item.fields.images.map(image => image.fields.file.url);
			let room = { ...item.fields, images, id };
			return room;
		});
		return tempItems;
	}

	/**
	 * @description
	 * @author Sujay Alaspure
	 * @param {string} slug
	 * @memberof RoomProvider
	 */
	getRoom = slug => {
		let tempRooms = [...this.state.rooms];
		const room = tempRooms.find(room => room.slug === slug);
		return room;
	};

	handleChange = event => {
		const target = event.target;
		const value = target.type === 'checkbox' ? target.checked : target.value;
		const name = event.target.name;
		this.setState(
			{
				[name]: value,
			},
			this.filterRooms,
		);
	};

	filterRooms = () => {
		let { rooms, type, capacity, price, minSize, maxSize, breakfast, pets } = this.state;
		let tempRooms = [...rooms];
		capacity = parseInt(capacity);
		price = parseInt(price);

		//filter by type
		if (type !== 'all') {
			tempRooms = tempRooms.filter(room => room.type === type);
		}
		//filter by capacity
		if (capacity !== 1) {
			tempRooms = tempRooms.filter(room => room.capacity >= capacity);
		}
		//filter by Price
		tempRooms = tempRooms.filter(room => room.price <= price);

		//filter by Size
		tempRooms = tempRooms.filter(room => room.size >= minSize && room.size <= maxSize);

		//filter by Breakfast
		if (breakfast) {
			tempRooms = tempRooms.filter(room => room.breakfast === true);
		}

		//Filter by Pets
		if (pets) {
			tempRooms = tempRooms.filter(room => room.pets === true);
		}

		//change State
		this.setState({
			sortedRooms: tempRooms,
		});
	};
	render() {
		return (
			<RoomContext.Provider
				value={{
					...this.state,
					getRoom: this.getRoom,
					handleChange: this.handleChange,
				}}>
				{this.props.children}
			</RoomContext.Provider>
		);
	}
}

const RoomConsumer = RoomContext.Consumer;

export function withRoomConsumer(Component) {
	return function ConsumerWrapper(props) {
		return <RoomConsumer>{value => <Component {...props} context={value} />}</RoomConsumer>;
	};
}

export { RoomConsumer, RoomContext, RoomProvider };
